<template>
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i>用户列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button type="primary" icon="delete" class="handle-del mr10" @click="newAdd">新增用户</el-button>
            </div>
            <el-table :data="allMessageData" border class="table" ref="multipleTable">
                <el-table-column prop="username" label="用户名" sortable>
                </el-table-column>
                <el-table-column prop="phone" label="手机号" sortable>
                </el-table-column>
                <el-table-column prop="email" label="邮箱" sortable>
                </el-table-column>
                <el-table-column prop="ctime" label="创建时间">
                </el-table-column>
                <!-- <el-table-column prop="uptime" label="更新时间">
                </el-table-column> -->
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click="editorTag(scope.$index, scope.row)">编辑</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="detailTags(scope.$index, scope.row)">删除</el-button> 
                        <el-button type="text" icon="el-icon-delete" class="red" @click="ModifyPass(scope.$index, scope.row)">修改密码</el-button> 
                    </template>
                </el-table-column>
            </el-table>
            


        </div>

        <!-- 编辑弹出框 -->
        <el-dialog :title="visibleTitle" :visible.sync="editVisible" width="500px" @close="closeDia">
            <el-form ref="form" :model="form" label-width="100px"  >
                <el-form-item label="用户名" >
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="手机号" >
                    <el-input v-model="form.phone"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" >
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
            
        </el-dialog>

        <el-dialog title="修改密码" :visible.sync="xiuPass" width="500px" @close="closeDia">
            <el-form ref="form" :model="formPass" label-width="100px"  >
                <el-form-item label="新密码" >
                    <el-input v-model="formPass.newPass"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" >
                    <el-input v-model="formPass.oldPass"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="xiuPass = false">取 消</el-button>
                <el-button type="primary" @click="savePass">确 定</el-button>
            </span>
            
        </el-dialog>

        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="400px">
            <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import axios from '../../axios'
    export default {
        name: 'basetable',
        data() {
            return {
                is_search: false,
                editVisible: false,
                delVisible: false,
                form: {
                    name:'',
                    email: '',
                    phone:'',
                },
                xiuPass:false,
                formPass:{
                    newPass:'',
                    oldPass:'',
                },
                picBannerUrl:'',
                picBannerUrlName:'',
                tenBlog:[],
                multipleSelection:[],
                isViewPic:false,
                bigImgUrl:'',
                isDetail:'',
                nowBannerId:'',

                visibleTitle:'',
                allTagsData:[],
                tagId:'',
                totalNum:0,
                pageSize:5,
                pageIndex:1,
                content: '',
                allMessageData:[],
                fileList: []

            }
        },
        components:{
            
        },
        created() {
            this.getAllTags();
            this.getBlogTenList();
        },
        computed: {
            data() {
                return this.tableData.filter((d) => {
                    let is_del = false;
                    for (let i = 0; i < this.del_list.length; i++) {
                        if (d.name === this.del_list[i].name) {
                            is_del = true;
                            break;
                        }
                    }
                    if (!is_del) {
                        if (d.address.indexOf(this.select_cate) > -1 &&
                            (d.name.indexOf(this.select_word) > -1 ||
                                d.address.indexOf(this.select_word) > -1)
                        ) {
                            return d;
                        }
                    }
                })
            }
        },
        methods: {
            // 看大图片
            bigImg(val){
                this.isViewPic=true;
                this.bigImgUrl=val
            },
            chaunSuccess(response, file, fileList){
                console.log(response.data.url, file, fileList)
                this.picBannerUrl = response.data.url;
                this.picBannerUrlName = response.data.name;
            },
           handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            //获取前十个博客列表
            getBlogTenList(){
                axios.get('/api/web/queryPageBlog',{
                    params:{
                        pageSize:10,
                        pageIndex:1,
                    }
                })
                        .then(res=>{
                            this.tenBlog=res.data.bloglist;
                            // this.totalNum=res.data.total;
                            console.log(this.blogListData,res);
                        })
            },
            //修改密码
            async ModifyPass(index, row){
                this.xiuPass=true;
                this.isDetail=row._id;
                
            },
            savePass(){
                this.formPass.newPass==this.formPass.oldPass?this.quePass():this.$message.error('密码不一样');
                this.quePass();
            },
            async quePass(){
                let data = await axios.post('/api/web/updatePassword',{id:this.isDetail,password:this.formPass.newPass})
                console.log('返回的信息：',data)
                if(data.status===1){
                    this.$message({
                        message: '恭喜你，修改成功',
                        type: 'success'
                    });
                    this.xiuPass=false;
                    this.formPass={newPass:'',oldPass:''}
                    this.isDetail='';
                }
            },
            // 获取banner列表
            getAllTags(){
                axios.get('/api/web/queryAllUser')
                    .then(res=>{
                        console.log(res)
                        this.allMessageData=res.data.rows;
                    }).catch(err=>{
                        console.log(err)
                    })
            },
            
        //    删除banner
            detailTags(index, row) {
                this.delVisible=true;
                this.isDetail=row._id;
            },
            detailBanner(row){
                axios.post('/api/web/dellUser',{
                    id:row
                }).then(res=>{
                    if(res.status==200){
                        this.delVisible = false;
                        this.$message({
                            showClose: true,
                            message: '恭喜你，删除成功！',
                            type: 'success'
                        });
                        this.isDetail='';
                        this.getAllTags();
                    }
                    console.log(res);
                })
            },
            // 确定删除
            deleteRow(){
                this.detailBanner(this.isDetail)
            },
            //新增标签
            newAdd(){
                
                this.nullBlog();
                this.visibleTitle='新增banner';
                this.editVisible = true;
                // this.insertBlog();
            },
            closeDia(){
                console.log('关闭了哈哈哈')
                this.fileList=[]
            },
            // 清除form内容
            nullBlog(){
                this.form={
                    title:'',
                    abstract:'',
                    // picUrl:'',
                    ctime: '',
                    region:'',
                    isStart:true,
                }
            },

            
            submit(){
                console.log(this.content);
                this.$message.success('提交成功！');
            },

            // 保存编辑
            saveEdit() {
                // this.$set(this.tableData, this.idx, this.form);
                // alert(this.form)
                console.log('hahh:',this.form)
                if(this.visibleTitle=='编辑banner'){
                    let subArr = {
                        // id:this.tagId,
                        // tag:this.form.tag,
                        // ctime:this.form.ctime || new Date(),
                        // uptime: new Date().toLocaleString("chinese", { hour12: false }),
                        id:this.nowBannerId,
                        name:this.form.name,
                        bannerImg:this.picBannerUrl,
                        bannerImgName:this.picBannerUrlName,
                        email:this.form.email,
                        isStart:this.form.isStart,
                        region:this.form.region,
                    }
                    console.log('主体信息',subArr);
                    axios.post('/api/web/updateOneBanner',subArr).then(res=>{
                        if(res.status==200){
                            this.$message({
                                showClose: true,
                                message: '恭喜你，更新成功！',
                                type: 'success'
                            });
                            this.editVisible = false;
                            this.nowBannerId='';
                            this.nullBlog();
                            this.getAllTags();
                        }
                        console.log('添加返回信息：',res);
                    })

                }else if(this.visibleTitle=='新增banner'){
                    let subArr = {
                        username:this.form.name,
                        phone:this.form.phone,
                        email:this.form.email,
                    }
                    this.addTags(subArr);
                    console.log('提交信息：',this.form)
                }
                
            },
            addTags(submitArr){
                axios.post('/api/web/addUser',submitArr).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，添加成功!',
                            type: 'success'
                        });
                        this.editVisible = false;
                        this.picBannerUrl='';
                        this.picBannerUrlName='';
                        this.nullBlog();
                        this.getAllTags();
                    }
                    console.log('添加返回信息：',res);
                })
                        
            },

            // 编辑标签
            editorTag(index,row){
                this.fileList=[]
                this.visibleTitle='编辑banner';
                this.editVisible = true;
                console.log('这是id:',row)
                this.nowBannerId = row._id;
                this.form={
                            name:row.username,
                            phone:row.phone,
                            email:row.email,
                        }
                this.picBannerUrl=row.bannerImg;
                this.picBannerUrlName=row.bannerImgName;
                this.fileList=[{
                    name:this.picBannerUrlName,
                    url:this.picBannerUrl
                }]

            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection);
            },

        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px; 
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
    .mr10{
        margin-right: 10px;
    }
    .bigTu{
        max-width: 1000px;
    }
</style>
